import { MessageBox } from 'mint-ui'
import '../../../static/css/zan/iconfont.css'
import LoadingImg from '../../public/loadingImg'
  export default {
      name: "mStorypage",
    data(){
        return{
          api: this.GLOBAL.baseURL,
          imageList:[],
          zanIndex:0,
          disList:[],
          storyData:''
        }
    },
    components:{
      LoadingImg,
    },
    methods:{
      lookMore(){
        MessageBox.alert('请前往APP或者客户端查看!')
      },
      goBack(){
        this.$router.go(-1)
      },
      //点赞
      addPraise(){
        this.$http.post(this.api + '/home/Comment/praise',{
          token: localStorage.getItem('token'),
          table_id: this.storyId,
          flag: 1,
          type: this.storyData.is_praise?2:1
        })
          .then(res=>{
            if(res.data.code == 1){
              this.getStory()
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.addPraise()
                })
            }else if(res.data.code == 0){
              MessageBox.alert(res.data.msg)
            }
          })
      },
      getStory(){
        this.$http.post(this.api + '/home/Story/get_story',{
          token: localStorage.getItem('token'),
          story_id: this.storyId,
          visit: 1
        })
          .then(res=>{
            if(res.data.code == 1){
              this.storyData = res.data.data
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getStory()
                })
            }else if(res.data.code == 0){
              MessageBox.alert(res.data.msg)
            }
          })
      },
      getLeave(val){
        this.$http.post(this.api + '/LeaveL',{
          token: localStorage.getItem('token'),
          table_id: this.storyId,
          flag: 2,
          order: 1,
          page:val
        })
          .then(res=>{
            if(res.data.code == 1){
              this.disList = res.data.data.data
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getLeave(val)
                })
            }else if(res.data.code == 0){
              MessageBox.alert(res.data.msg)
            }
          })
      },
      clickPrease(item,index){
        this.$http.post(this.api + '/home/Comment/praise',{
          token: localStorage.getItem('token'),
          flag: 4,
          table_id: item.msg_id,
          type: item.is_praise?2:1
        })
          .then(res=>{
            if(res.data.code == 1){
              this.getLeave(1)
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.clickPrease(item,index)
                })
            }else if(res.data.code == 0){
              MessageBox.alert(res.data.msg)
            }
          })
      }

    },
    mounted(){
      this.getLeave(1)
    },
    created(){
      this.storyId = this.$route.query.information
      this.getStory()
      window.scroll(0,0)
      let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
      if(!flag){
        this.$router.push({
          path: '/storyPage',
          query:{
            information: this.$route.query.information
          }
        })
      }
    }
  }



